import { Component, OnInit ,ViewChild , ElementRef} from '@angular/core';
import {Platform,NavController} from '@ionic/angular';
//import { GoogleMaps, GoogleMap} from '@ionic-native/google-maps/ngx';
import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  LatLng,
  MarkerOptions,
  Marker
} from "@ionic-native/google-maps";

@Component({
  selector: 'app-map-home',
  templateUrl: './map-home.page.html',
  styleUrls: ['./map-home.page.scss'],
})
export class MapHomePage implements OnInit {
  map: GoogleMap;
  //@ViewChild('map') element;
  //@ViewChild('map') mapElement: ElementRef;
  // angular 8 :https://stackoverflow.com/questions/56704164/angular-viewchild-error-expected-2-arguments-but-got-1
  @ViewChild('map', {static: false}) mapElement:ElementRef;
  constructor(public googleMaps: GoogleMaps, public plt: Platform,
              public nav: NavController) {
  }
  ngOnInit() {
    // await this.platform.ready();
    // await this.loadMap();
  }
  ngAfterViewInit() {
    this.plt.ready().then(() => {
      this.initMap();
    });
  }

  initMap() {

    let map: GoogleMap = this.googleMaps.create(this.mapElement.nativeElement);

    map.one(GoogleMapsEvent.MAP_READY).then((data: any) => {

      let coordinates: LatLng = new LatLng(33.6396965, -84.4304574);

      let position = {
        target: coordinates,
        zoom: 17
      };

      map.animateCamera(position);

      let markerOptions: MarkerOptions = {
        position: coordinates,
        icon: "assets/images/icons8-Marker-64.png",
        title: 'Our first POI'
      };

      const marker = map.addMarker(markerOptions)
        .then((marker: Marker) => {
          marker.showInfoWindow();
      });
    })
  }
}
